body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.black {
    color: #333;
    text-shadow: 0 0 1px white;
}

.white {
    color: white;
    text-shadow: 0 0 1px black;
}

.tile {
    position: relative;
    width: 1em;
    height: 1em;

    padding: 0.2em;
    line-height: 1em;
    text-align: center;

    background-color: #ddd;
}

.tile:after {
    position: relative;
    z-index: 1;
}

.row:nth-of-type(even) .tile:nth-of-type(odd),
.row:nth-of-type(odd) .tile:nth-of-type(even) {
    background-color: #777;
}

.selected:before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;

    opacity: 0.5;
    background-color: #aa0;
    z-index: 0;
}

.legal:before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;

    opacity: 0.5;
    background-color: #0aa;
    z-index: 0;
}

.king:after {
    content: '\265A';
}

.queen:after {
    content: '\265B';
}

.bishop:after {
    content: '\265D';
}

.rook:after {
    content: '\265C';
}

.knight:after {
    content: '\265E';
}

.pawn:after {
    content: '\2659';
}

.board {
    -webkit-transition: -webkit-transform 2s cubic-bezier(.27,.91,.44,1);
    transition: -webkit-transform 2s cubic-bezier(.27,.91,.44,1);
    transition: transform 2s cubic-bezier(.27,.91,.44,1);
    transition: transform 2s cubic-bezier(.27,.91,.44,1), -webkit-transform 2s cubic-bezier(.27,.91,.44,1);
}

.board .row {
    display: flex;
    flex-direction: row;
    margin: 0 auto;

    font-size: 2em;
}

.board.black {
    -webkit-transform: translate3d(0,0,0) rotate(180deg);
            transform: translate3d(0,0,0) rotate(180deg);
    -webkit-animation: fade-out-in 2s cubic-bezier(.27,.91,.44,1);
            animation: fade-out-in 2s cubic-bezier(.27,.91,.44,1);
}

.board.black .tile {
    -webkit-transform: translate3d(0,0,0) rotate(-180deg);
            transform: translate3d(0,0,0) rotate(-180deg);
}
.App {
  text-align: center;

  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
